import star from "../asists/المولات والمحالات/Img background.png";
import logo from "../asists/img project/لوجو المستخدم.png";
import { Carousel } from "primereact/carousel";
import "./DoctorCard.css";
export default function DoctorCard(props) {
  return (
    <div className="doctor__card d-flex flex-wrap justify-content-between">
      <div className="doctor__info d-flex col-lg-5 col-md-12 flex-wrap">
        <div className="doctor__img col-sm-12 col-lg-4">
          <img src={props.img} />
        </div>
        <div className="doctor__data">
          <p className="doctor__name">{props.name}</p>
          <p className="doctor__specialty">{props.specialty}</p>
          <div className="stars d-flex">
            <img src={star} />
            <img src={star} />
            <img src={star} />
            <img src={star} />
            <img src={star} />
          </div>
          <p className="doctor__rate">التقييم العام من 5 زوار للدكتور</p>
          <h2>
            <img src={logo} /> دكتور : {props.specialtyName}
          </h2>
          <h2>
            <img src={logo} /> كشف : {props.scanState}
          </h2>
          <h2>
            <img src={logo} /> الأماكن : {props.place}
          </h2>
        </div>
      </div>
      <div className="doctor__time d-flex justify-content-between col-lg-5 col-md-12">
        <div
          className={props.dayAva ? "time__holder" : "time__holder inactive"}
        >
          <p className="doctor__day">اليوم</p>
          <p className="doctor__date">
            {props.dayAva ? `${props.date}` : "لايوجد مواعيد متاحه الان"}
          </p>
          <p className="doctor__ava">{props.dayAva ? "متاح" : "غير متاح"}</p>
        </div>
        <div
          className={props.dayAva ? "time__holder" : "time__holder inactive"}
        >
          <p className="doctor__day">اليوم</p>
          <p className="doctor__date">
            {props.dayAva ? `${props.date}` : "لايوجد مواعيد متاحه الان"}
          </p>
          <p className="doctor__ava">{props.dayAva ? "متاح" : "غير متاح"}</p>
        </div>
        <div
          className={props.dayAva ? "time__holder" : "time__holder inactive"}
        >
          <p className="doctor__day">اليوم</p>
          <p className="doctor__date">
            {props.dayAva ? `${props.date}` : "لايوجد مواعيد متاحه الان"}
          </p>
          <p className="doctor__ava">{props.dayAva ? "متاح" : "غير متاح"}</p>
        </div>
      </div>
    </div>
  );
}
